import { Component } from 'react'
import PropTypes from 'prop-types';

class Summary extends Component {
    constructor(props) {
        super(props);
        this.propTypes = {
            number: PropTypes.number.isRequired,
            total: PropTypes.number.isRequired,
            onHandleSettlement: PropTypes.func
        }
    }

    handleClick(e) {
        const { number, total } = this.props;
        this.props.onHandleSettlement(number,total);
    }

    render() {
        const { number, total } = this.props;
        return (
            <div className="sidebarcart-summary">
                <p><span className="color-stress">{number}</span>,<span className="color-stress">{total}</span></p>
                <button className="sidebarcart-submit" onClick={this.handleClick.bind(this)}></button>
            </div>
            );
    }
}

export default Summary;


