import { Component } from 'react';
import { connect } from 'react-redux'
import { Link } from 'react-router';
import Location from './containers/main/location'
import FullText from './containers/main/fulltext'
import ShowTab from './containers/main/showtab'
import Tab from './containers/main/tab'
import Business from './containers/main/business'
import SidebarTab from './containers/sidebar/sidebartab'
import SidebarContent from './containers/sidebar/sidebarcontent'
require('es6-promise');
import { fetchGetBusiness } from './actions/home'

const businessFilter = [
    { key: 0, FilterName: '', active: '' },
    { key: 1, FilterName: '', active: '' },
    { key: 2, FilterName: '', active: '' },
    { key: 3, FilterName: '', active: '' },
    { key: 4, FilterName: '', active: '' },
    { key: 5, FilterName: '', active: '' },
    { key: 6, FilterName: '', active: '' },
    { key: 7, FilterName: '', active: '' },
    { key: 8, FilterName: '', active: '' },
    { key: 9, FilterName: '', active: '' },
    { key: 10, FilterName: '', active: '' },
    { key: 11, FilterName: '', active: '' },
];

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.handleSearch = this.handleSearch.bind(this);
        this.handleFilter = this.handleFilter.bind(this);

        this.state = {
            sideType: '',
            transform: 'translate3d(0px, 0px, 0px)',
            showSide: false
        }
    }

    componentDidMount() {
        const { dispatch } = this.props;
        dispatch(fetchGetBusiness())
    }

    handleSearch(searchModel) {
        console.log(searchModel);
    }

    handleFilter(index) {
        console.log(index)
    }

    handleTypeClick(type) {
        console.log(type)
        if (!this.state.showSide) {
            this.setState({ sideType: type, transform: 'translate3d(-295px, 0px, 0px)', showSide: true });
        }
        else {
            this.setState({ sideType: type, transform: 'translate3d(0px, 0px, 0px)', showSide: false });
        }
    }

    render() {
        const locationName = '';
        let { items } = this.props;
        return (
            <div>
                <div className="sidebar" role="complementary" style={{ transform: this.state.transform }}>
                    <SidebarTab onHandleTypeChange={this.handleTypeClick.bind(this)} />
                    <SidebarContent type={this.state.sideType}/>
                </div>
                <div className="main">
                    <div className="container clearfix">
                        <Location location={locationName} />
                        <FullText onHandleClick={this.handleSearch} />
                    </div>
                    <div className="place-tab clearfix container" style={{ marginLeft: '305px' }}>
                        <ShowTab />
                    </div>
                    <div className="container">
                        <Tab
                            filter={businessFilter}
                            onHandleFilter={this.handleFilter}
                        />
                        <Business business={items} />
                    </div>
                </div>
            </div>
        );
    }
}

function mapStateToProp(state) {
    return {
        items: state.getBusiness.items
    }
}

export default connect(mapStateToProp)(Home);


